// /* ===== Qobilatlarim ===== */
.qobilat {
    margin: 0;
    padding-top: 20px;
    height: 100vh;
    display: flex;
    // align-items: center;
    // display: flex;
    // flex-direction: column;
    justify-content: space-between;

    h1 {
        font-size: 40px;
        font-weight: 900;
        color: whitesmoke;
        font-family: Georgia, Times, 'Times New Roman', serif;
        margin-top: 40px;
        margin-bottom: 40px;
        text-align: center;
    }

    .btn-update{
        display:none;
      }
      @media only screen and (max-width:600px) and (min-width:400px){
        .progres{
          width:45%;
          float:left;
          margin:0 1%;
        }
    }
    @media only screen and (min-width:600px){
        .progres{
         
          width:20%;
          float:left;
          margin:0 2.5%;
        }
      }
      @media only screen and (max-width:400px){
        .progres{
          width:90%;
          float:left;
          margin:0 1%;
        }
      }
      .counter {
          display: inline-flex;
          cursor:pointer;
          width:300px;
          height:300px;
          max-width:100%;
          position:relative;
          justify-content:center;
          align-items:center;
          font-size: calc(1em + 1vmin);
          transition: height .2s ease-in-out;
          // background: #fff;
          background:url('../image/logos/1.html5.png') no-repeat    ;
        border-radius:50%;
        box-shadow:0px 1px 10px 2px rgba(0,0,0,0.2);
        margin:1em 0;
      }
      .percentage {
        position:absolute;
        text-align:center;
        top:50%;
        left:0;
        right:0;
        vertical-align:middle;
        transform:translate3d(0,-50%,0);
      }
      canvas {
        position:absolute;
        top:0;
        left:0;
      }
      input {
          width: 200px;
      }
      @import url('https://fonts.googleapis.com/css?family=Open+Sans');
      body {
        font-family: 'Open Sans', sans-serif;
        text-align:center;
      }
      .bg-style {
        padding:2em;
      }
}